<template>
 <div id="home">
   <nav-bar class="nav-bar"><div slot="center"></div></nav-bar>
   <tab-control :titles="titles" class="tabControl"  @tabClick="tabClick" ref="tabControl1" v-show="isTabFixed"></tab-control>
   <scroll ref="scroll" class="scroll" :probeType="3" :pullUpLoad="true" @scroll="scroll"  @pullingUp="pullingUp" >
     <home-swiper :banners="banners" @SwiperImgLoad="SwiperImgLoad"></home-swiper>
     <home-recommend :recommends="recommends"></home-recommend>
     <home-feature></home-feature>
     <tab-control :titles="titles" @tabClick="tabClick" ref="tabControl2"></tab-control>
     <goods-list :goods="showGoods"></goods-list>
   </scroll>
   <back-top v-show="isshow" @click.native="backTop"></back-top>
 </div>
</template>

<script>
  import homeSwiper from "./childComps/homeSwiper";
  import homeRecommend from "./childComps/homeRecommend";
  import homeFeature from "./childComps/homeFeature";

  import NavBar from "components/common/navbar/NavBar";
  import Scroll from "components/common/scroll/Scroll";
  import TabControl from "components/content/tabControl/TabControl";
  import GoodsList from "components/content/goods/GoodsList";

  import {getHomeMultidata,getHomeGoods} from "network/home";
  import {itemListenrMixin,backTopMixin} from "common/mixin";

  export default {
    name: "Home",
    data() {
      return {
        banners: [],
        recommends: [],
        titles: ['','',''],
        goods: {
          'pop': {page: 0, list: []},
          'new': {page: 0, list: []},
          'sell': {page: 0, list: []}
        },
        currentType: 'pop',
        isTabFixed: false,
        saveY: 0,
      }
    },
    computed: {
      showGoods() {
        return this.goods[this.currentType].list
      }
    },
    components: {
      homeSwiper,
      homeRecommend,
      homeFeature,
      NavBar,
      Scroll,
      TabControl,
      GoodsList,
    },
    mixins: [itemListenrMixin,backTopMixin],
    created() {
      this._getHomeMultidata()
      this._getHomeGoods('pop')
      this._getHomeGoods('new')
      this._getHomeGoods('sell')
    },
    activated() {
      this.$refs.scroll.ScrollTo(0,this.saveY,0)
      this.$refs.scroll.refresh()
    },
    deactivated() {
      this.saveY = this.$refs.scroll.saveY()
      //goodsListItem 
      this.$bus.$off('imageLoad',this.itemListener)

    },
    methods: {

      //
      _getHomeMultidata() {
        getHomeMultidata().then(res => {
          // console.log(res);
          this.banners = res.data.banner.list
          this.recommends = res.data.recommend.list
        })
      },
      _getHomeGoods(type) {
        const page = this.goods[type].page + 1
        getHomeGoods(type,page).then(res => {
          // console.log(res);
          this.goods[type].list.push(...res.data.list)
          // console.log(this.goods[type].list);
          this.goods[type].page += 1
          //pullingUp finishPullUp() BetterScroll  pullUp
          this.$refs.scroll.finishPullUp()
        })
      },
      //
      //tabControl,goods
      tabClick(index) {
        switch (index) {
          case 0:
            this.currentType = 'pop'
            break
          case 1:
            this.currentType = 'new'
            break
          case 2:
            this.currentType = 'sell'
            break
        }

        this.$refs.tabControl1.currentIndex = index
        this.$refs.tabControl2.currentIndex = index
      },
      //1000,backTop
      scroll(position) {
        //1000back-top
        this.isshow = (-position.y) > 1000
        // this.$refs.tabControl2.$el.offsetToptabControl
        this.isTabFixed = (-position.y) > this.$refs.tabControl2.$el.offsetTop
      },
      //
      pullingUp() {
        this._getHomeGoods(this.currentType)
      },
      //
      SwiperImgLoad() {
        // console.log(this.$refs.tabControl2.$el.offsetTop);

      }
    }
  }
</script>

<style scoped>
  #home {
    /*padding-top: 44px;*/
    height: 100vh;
    position: relative;
  }
  .nav-bar {
    background-color: var(--color-tint);
    color: #ffffff;
  }
  .tabControl {
    position: relative;
    z-index: 9
  }
  .scroll {
    overflow: hidden;
    position: absolute;
    top: 44px;
    bottom: 49px;
    left: 0;
    right: 0;
  }
</style>